import Fns from './course-info-fns.mjs';

const CS444_WEIGHTS = {
    '$qAvg': 1,
    '$pAvg': 0.35,
    '$hAvg': 0.25,
    'mid': 0.13,
    'fin': 0.15,
};

const CS544_WEIGHTS = {
  '$qAvg': 1,
  '$pAvg': 0.35,
  '$hAvg': 0.22,
  'pap': 1,
  'mid': 0.13,
  'fin': 0.15,
};

//These are dummy values
const LETTER_GRADE_CUTOFFS = [
  [96, 'A'],
  [90.5, 'A-'],
  [85.3, 'B+'],
  [80.5, 'B' ],
  [77, 'B-'],
  [70, 'C+'],
  [65, 'C' ],
  [60, 'C-'],
  [50, 'D' ],
  [0, 'F' ],
];

function rowAggregate(regex, aggregateId) {
  return {
    aggregateId,
    filters: [
      Fns.colIdMatchFilter(regex),
      Fns.minDropFilter(1),
    ],
    aggregateFn: Fns.avgAggregate(false),
  };
}

const CS544_INFO = {
  id: 'cs544',
  name: 'Programming for the Web',

  rawColInfos: [
    { colIdRegex: /^emailId$/, contentRegex: /^\w+$/, },
    { colIdRegex: /^section$/, contentRegex: /^\w+$/, },
    { colIdRegex: /^prj\d+$/, },
    { colIdRegex: /^hw\d+$/, },
    { colIdRegex: /^qz\d+$/, hi: 12, },
    { colIdRegex: /^mid$/, },
    { colIdRegex: /^fin$/, },
    { colIdRegex: /^pap$/, hi: 3, },
  ],

  rowAggregates: [
    rowAggregate(/^prj\d+$/, '$pAvg'),
    rowAggregate(/^hw\d+$/, '$hAvg'),
    rowAggregate(/^qz\d+$/, '$qAvg'),
    { aggregateId: '$total',
      aggregateFn: function(row) {
	return (row.section === 'cs444')
	  ? Fns.weightedSumAggregate(CS444_WEIGHTS)(row)
	  : Fns.weightedSumAggregate(CS544_WEIGHTS)(row);
      },
    },
    { aggregateId: '$grade',
      aggregateFn: Fns.cutoffAggregate('$total', LETTER_GRADE_CUTOFFS),
    },
  ],

  statColId: '$stats',
  colAggregates: [
    { aggregateId: '$avg',
      aggregateFn: Fns.avgAggregate(true),
    },
    { aggregateId: '$min',
      aggregateFn: Fns.minAggregate(true),
    },
    { aggregateId: '$max',
      aggregateFn: Fns.maxAggregate(true),
    },
    { aggregateId: '$count',
      aggregateFn: Fns.countAggregate(true),
    },
  ],
};
export default CS544_INFO;


