import Fns from './course-info-fns.mjs';

const LAB_TOTAL = 7;
const WEIGHTS = {
  'talk': 1,
  '$lab': 1,
  '$qAvg': 1, 
  '$pAvg': 0.24,
  '$hAvg': 0.20,
  'mid': 0.15,
  'fin': 0.20,
};

//These are dummy values
const LETTER_GRADE_CUTOFFS = [
  [95, 'A'],
  [90, 'A-'],
  [85, 'B+'],
  [80, 'B' ],
  [77, 'B-'],
  [70, 'C+'],
  [65, 'C' ],
  [60, 'C-'],
  [50, 'D' ],
  [0, 'F' ],
];

function rowAggregate(regex, aggregateId) {
  return {
    aggregateId,
    filters: [
      Fns.colIdMatchFilter(regex),
      Fns.minDropFilter(1),
    ],
    aggregateFn: Fns.avgAggregate(false),
  };
}

const CS471_INFO = {
  id: 'cs471',
  name: 'Programming Languages',

  rawColInfos: [
    { colIdRegex: /^emailId$/, contentRegex: /^\w+$/, },
    { colIdRegex: /^prj\d+$/, },
    { colIdRegex: /^hw\d+$/, },
    { colIdRegex: /^qz\d+$/, hi: 12, },
    { colIdRegex: /^lab\d+$/, lo: -1, hi: 0, },
    { colIdRegex: /^mid$/, },
    { colIdRegex: /^fin$/, },
    { colIdRegex: /^talk$/, hi: 3, },
  ],

  rowAggregates: [
    rowAggregate(/^prj\d+$/, '$pAvg'),
    rowAggregate(/^hw\d+$/, '$hAvg'),
    rowAggregate(/^qz\d+$/, '$qAvg'),
    { aggregateId: '$lab',
      filters: [
	Fns.colIdMatchFilter(/^lab\d+$/),
      ],
      aggregateFn: row => {
	const sum = Object.values(row).reduce((acc, v) => Fns.val(v) + acc, 0);
	return LAB_TOTAL + sum < 0 ? 0 : LAB_TOTAL + sum;
      },
    },
    { aggregateId: '$total',
      aggregateFn: Fns.weightedSumAggregate(WEIGHTS),
    },
    { aggregateId: '$grade',
      aggregateFn: Fns.cutoffAggregate('$total', LETTER_GRADE_CUTOFFS),
    },
  ],

  statColId: '$stats',
  colAggregates: [
    { aggregateId: '$avg',
      aggregateFn: Fns.avgAggregate(true),
    },
    { aggregateId: '$min',
      aggregateFn: Fns.minAggregate(true),
    },
    { aggregateId: '$max',
      aggregateFn: Fns.maxAggregate(true),
    },
    { aggregateId: '$count',
      aggregateFn: Fns.countAggregate(true),
    },
  ],
};
export default CS471_INFO;

