import cs471 from './cs471-info.mjs';
import cs544 from './cs544-info.mjs';

const COURSE_INFOS = { cs471, cs544 };
const COURSE_IDS = Object.keys(COURSE_INFOS);

export default function getCourseInfo(courseId) {
  const info = COURSE_INFOS[courseId];
  if (info) {
    return info;
  }
  else {
    const message = `unknown course ${courseId}`;
    const options = { code: 'NOT_FOUND' };
    return {
      errors: [ { message, options,
		  //backward compatibility with prj3
		  _msg: message, _options: options,
		}
	      ],
    };
  }
}

function getCourseIds() { return COURSE_IDS; }
export {  getCourseInfo, getCourseIds };

